<template xmlns="http://www.w3.org/1999/html">
  <div>
    <h3>   ( ): </h3>
    <div>
      <button @click="selectUploadFile()"></button>
    </div>
    <div>
      <button @click="selectReply()"></button>
    </div>
    <div>
      <button @click="selectUploadFileForMain()">  </button>
    </div>
    <div>
      <button @click="selectUploadFileForTitle()">  </button>
    </div>
    <div>
      <button @click="updateNickname()"> test</button>
    </div>

    <span>response : {{ response }}</span>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: "FileUpload", // 
  components: {}, //    import( )
  data() { //html js    
    return {
      response: ""
    };
  },
  setup() {
  }, // API
  created() {
  }, //  
  mounted() {
  }, //template  html    
  unmounted() {
  }, //unmount   
  methods: {
    selectUploadFile() {
      var vue = this
      let elem = document.createElement('input')
      //    /    
      elem.id = 'image'
      elem.type = 'file'
      elem.accept = 'image/*'
      elem.multiple = true
      // 
      elem.click();
      //  
      elem.onchange = function () {
        const formData = new FormData()
        formData.append('userId', 2);
        formData.append('boardId', 1);
        formData.append('subject', ' ');
        formData.append('content', 'Testing contents')
        formData.append('ref', 0);
        formData.append('depth', 0);
        for (var index = 0; index < this.files.length; index++) {
          formData.append('files', this.files[index])
        }
        axios.post('/api/v1/post/write', formData, {headers: {'Content-Type': 'multipart/form-data'}}).then(response => {
          vue.response = response.data
        }).catch(error => {
          vue.response = error.message
        })
      }

    },
    selectReply() {
      var vue = this
      let elem = document.createElement('input')
      //    /    
      elem.id = 'image'
      elem.type = 'file'
      elem.accept = 'image/*'
      elem.multiple = true
      // 
      elem.click();
      //  
      elem.onchange = function () {
        const formData = new FormData()
        formData.append('userId', 2);
        formData.append('boardId', 1);
        formData.append('subject', '!!');
        formData.append('content', '....')
        formData.append('ref', 7);
        formData.append('depth', 4);
        for (var index = 0; index < this.files.length; index++) {
          formData.append('files', this.files[index])
        }
        axios.post('/api/v1/post/reply', formData, {headers: {'Content-Type': 'multipart/form-data'}}).then(response => {
          vue.response = response.data
        }).catch(error => {
          vue.response = error.message
        })
      }

    },
    selectUploadFileForMain() {
      var vue = this
      let elem = document.createElement('input')
      //    /    
      elem.id = 'image'
      elem.type = 'file'
      elem.accept = 'image/*'
      elem.multiple = true
      // 
      elem.click();
      //  
      elem.onchange = function () {
        const formData = new FormData()
        formData.append('userId', 2);
        formData.append('boardId', 1);
        formData.append('subject', ' ');
        formData.append('content', '')
        for (var index = 0; index < this.files.length; index++) {
          formData.append('files', this.files[index])
        }
        axios.post('/api/v1/main/save', formData, {headers: {'Content-Type': 'multipart/form-data'}}).then(response => {
          vue.response = response.data
        }).catch(error => {
          vue.response = error.message
        })
      }

    },
    selectUploadFileForTitle() {
      var vue = this
      let elem = document.createElement('input')
      //    /    
      elem.id = 'image'
      elem.type = 'file'
      elem.accept = 'image/*'
      elem.multiple = true
      // 
      elem.click();
      //  
      elem.onchange = function () {
        const formData = new FormData()
        formData.append('userId', 2);
        formData.append('boardId', 1);
        formData.append('subject', ' ');
        formData.append('content', '')
        for (var index = 0; index < this.files.length; index++) {
          formData.append('files', this.files[index])
        }
        axios.post('/api/v1/title/save', formData, {headers: {'Content-Type': 'multipart/form-data'}}).then(response => {
          vue.response = response.data
        }).catch(error => {
          vue.response = error.message
        })
      }

    },
    updateNickname() {
      var vue = this
      let elem = document.createElement('input')
      elem.type = 'text'

      const formData = new FormData()
      formData.append('id', 1);
      formData.append('nickname', '');
      axios.patch('/api/v1/user/myinfo/update', formData).then(response => {
        vue.response = response.data
      }).catch(error => {
        vue.response = error.message
      })


    }
  } //    
}
</script>

